@columns: 12;
@atom: (100% / @columns);

@break_lg: 1200px;
@break_md: 992px;
@break_sm: 720px;
@break_xs: 576px;

@offset: 30px;
@offset_lg: @offset;
@offset_md: @offset;
@offset_sm: @offset;
@offset_xs: @offset;

@offset_one_side: (@offset / 2);
@offset_lg_one_side: (@offset_lg / 2);
@offset_md_one_side: (@offset_md / 2);
@offset_sm_one_side: (@offset_sm / 2);
@offset_xs_one_side: (@offset_xs / 2);

@fields: 30px;
@fields_md: 15px;

.wrapper-full() {
  padding-left: @fields;
  padding-right: @fields;

  .md-block({padding-left: @fields_md; padding-right: @fields_md;});
}

.row-offsets() {
  margin-left: (@offset_one_side * -1);
  margin-right: (@offset_one_side * -1);
}

.col-offsets(@type) {
  @{type}-left: @offset_one_side;
  @{type}-right: @offset_one_side;
}

.wrapper() {
  max-width: 1532px;
  margin: 0 auto;
  .wrapper-full();
}

.row-flex() {
  display: flex;
  flex-wrap: wrap;
  .row-offsets();
}

.row-float() {
  .row-offsets();
  .clearfix();
}

.row-ib() {
  .row-offsets();
}

.col() {
  box-sizing: border-box;
  word-wrap: break-word;
  .col-offsets(margin);
}

.col-float() {
  float: left;
}

.col-ib() {
  display: inline-block;
  vertical-align: top;
}

.col-padding() {
  word-wrap: break-word;
  .col-offsets(padding);
}

.size(@n) {
  @val: 100% / @columns * @n;
  width: ~'calc(@{val} - @{offset})';
}

.size-lg(@n) {
  @val: 100% / @columns * @n;
  .lg(width, ~'calc(@{val} - @{offset_lg})');
}

.size-md(@n) {
  @val: 100% / @columns * @n;
  .md(width, ~'calc(@{val} - @{offset_md})');
}

.size-sm(@n) {
  @val: 100% / @columns * @n;
  .sm(width, ~'calc(@{val} - @{offset_sm})');
}

.size-xs(@n) {
  @val: 100% / @columns * @n;
  .xs(width, ~'calc(@{val} - @{offset_xs})');
}

.shift(@n) {
  @val: 100% / @columns * @n;
  margin-left: ~'calc(@{val} + @{offset_one_side})';
  margin-right: ~'calc(@{val} + @{offset_one_side})';
}

.shift-left(@n) {
  @val: 100% / @columns * @n;
  margin-left: ~'calc(@{val} + @{offset_one_side})';
}

.shift-right(@n) {
