.lg(@name, @value) {
  @media screen and (max-width: @break_lg) {
    @{name}: @value;
  }
}

.lg-block(@content) {
  @media screen and (max-width: @break_lg) {
    @content();
  }
}

.md(@name, @value) {
  @media screen and (max-width: @break_md) {
    @{name}: @value;
  }
}

.md-block(@content) {
  @media screen and (max-width: @break_md) {
    @content();
  }
}

.sm(@name, @value) {
  @media screen and (max-width: @break_sm) {
    @{name}: @value;
  }
}

.sm-block(@content) {
  @media screen and (max-width: @break_sm) {
    @content();
  }
}

.xs(@name, @value) {
  @media screen and (max-width: @break_xs) {
    @{name}: @value;
  }
}

.xs-block(@content) {
  @media screen and (max-width: @break_xs) {
    @content();
  }
}

.from(@min_width, @content) {
  @media screen and (min-width: @min_width) {
    @content();
  }
}

.to(@max_width, @content) {
  @media screen and (max-width: @max_width) {
    @content();
  }
}

.from-to(@min_width, @max_width, @content) {
  @media screen and (min-width: @min_width) and (max-width: @max_width) {
    @content();
  }
}

.u-row-flex(@offset) {
  display: flex;
  flex-wrap: wrap;
  margin-left: (@offset / -2);
  margin-right: (@offset / -2);
}

.u-col(@offset) {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: (@offset / 2);
  margin-right: (@offset / 2);
}

.u-size(@n, @columns, @offset) {
  width: ~'calc(100% / @{columns} * @{n} - @{offset})';
}

.reset() {
  body,
  div,
  dl,
  dt,
  dd,
  ul,
  li,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  pre,
  code,
  form,
  fieldset,
  input,
  textarea,
  p,
  blockquote,
  th,
  td {
    margin: 0;
    padding: 0;
  }
  table {
    border-collapse: collapse;
    border-spacing: 0;
  }
  fieldset,
  img,
  abbr {
    border: 0;
  }
  address,
  caption,
  cite,
  code,
  dfn,
  em,
  strong,
  th,
  var {
    font-style: normal;
    font-weight: normal;
  }
  ul li {
    list-style: none;
  }
  caption,
  th {
    text-align: left;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-size: 100%;
    font-weight: normal;
  }
  sup {
    vertical-align: text-top;
  }
  sub {
    vertical-align: text-bottom;
  }
  input,
  textarea,
  select {
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
  }
  legend {
    color: #000;
  }
  article,
  aside,
  details,
  figcaption,
  figure,
  footer,
  header,
  hgroup,
  menu,
  nav,
  section,
  main {
    display: block;
  }
  img {
    max-width: 100%;
    height: auto;
  }
}

.debug(@background, @outline) {
  height: 100vh;
  position: fixed;
  width: 100%;

  > div {
    height: inherit;
    .wrapper();

    > div {
      height: inherit;
      .row-flex();

      > div {
        .col();
        .size(1);
        height: inherit;
        outline: @outline;
        background: @background;
      }
    }
  }
}

.clearfix() {
  &::after {
    display: block;
    content: '';
    clear: both;
  }
}
